import './js/header';
import './js/mobile-menu';
import './js/hero';
import './js/book';
import './js/book-modal';
import './js/feedback';
import './js/events';
import './js/contact-modal';

import { openBookModal } from './js/book-modal.js';

document.querySelector('.books')?.addEventListener('click', async e => {
  const btn = e.target.closest('.btn');
  if (!btn) return;

  const bookId = btn.dataset.id;
  if (!bookId) return;

  try {
    const response = await fetch(
      `https://books-backend.p.goit.global/books/${bookId}`
    );
    const data = await response.json();

    const bookData = {
      image: data.book_image,
      title: data.title,
      author: data.author,
      price: data.price,
      description: data.description,
      shipping: '–ë–µ–∑–∫–æ—à—Ç–æ–≤–Ω–∞ –¥–æ—Å—Ç–∞–≤–∫–∞',
      returns: '–ü–æ–≤–µ—Ä–Ω–µ–Ω–Ω—è –ø—Ä–æ—Ç—è–≥–æ–º 14 –¥–Ω—ñ–≤',
    };

    openBookModal(bookData);
  } catch (error) {
    console.error('–ü–æ–º–∏–ª–∫–∞ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ –∫–Ω–∏–≥–∏:', error);
  }
});
